body {
  font-size: 1.25em;
}

a {
  text-decoration: none;
}

a,
input {
  z-index: 1;
  position: relative;
}

pre {
  width: 100%;
  overflow: scroll;
}

section {
  margin-bottom: 1em;
}

.contents {
  display: grid;
}

img {
  max-width: 100%;
}

img.active {
  max-height: 100vh;
  width: auto;
  height: auto;
}

.scrub .canvasWrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

canvas {
  max-height: 100vh;
  max-width: 100vw;
}

figcaption {
  font-size: small;
  font-family: monospace;
}